.footer-menu__link {
  @apply text-blue-900 font-semibold lg:font-medium;
  @apply text-xl/8 lg:text-sm xl:text-[17px]/3.5;
  @apply lg:py-3 lg:px-2;
  @apply xl:p-3;
  @apply rounded-10 transition-colors;
  &:hover, &:active, &:focus {
    @apply text-blue-900 lg:no-underline lg:bg-slate-40;
  }
}
.footer-menu_mobile {
  @apply relative;
  &::after {
    @apply md:content-[''];
    @apply absolute -right-[21px] top-0 bottom-0 w-px;
    @apply bg-slate-150;
  }
}
.footer-review::after {
  content: '';
  width: 70px;
  height: 62px;
  position: absolute;
  right: 20px;
  top: -16px;
  mask: center no-repeat url("../img/quote.svg");
  mask-size: cover;
  background-color: #F4F6FA;
  @apply transition-all;
}
.footer-review:hover::after {
  top: 20px;
  background-color: #E8ECF3;
}
.footer__word-logo {
  @apply aspect-[1392/278] text-slate-50;
  @apply overflow-hidden;
  @apply relative;
  mask-image: url("../img/word_logo.svg");
  mask-size: cover;
  background-color: currentColor;
  &::after {
    content: '';
    @apply absolute w-full h-[150px] -bottom-[90px];
    background: #FFF;
    filter: blur(35px);
  }
}
.footer-features__check_1 {
  background-image: url("../img/footer-features/check_1.svg");
  background-position: center bottom;
}
.footer-features__check_2 {
  background-image: url("../img/footer-features/check_2.svg");
  background-position: center bottom;
}
.footer-features__check_3 {
  background-image: url("../img/footer-features/check_3.svg");
  background-position: center bottom;
}
