.footer-features {
  @apply w-[1136px] 2xl:w-[1375px] 3xl:w-[1536px];
  @apply mb-9 lg:mb-8 xl:mb-9 2xl:mb-10;
  @apply mx-auto z-1;
  @apply max-xl:zoom-[calc(984/1136)];
  @apply max-lg:zoom-[calc(728/1136)];
}
.footer-features__label {
  @apply font-serif font-bold;
  @apply text-[66px] 2xl:text-[80px] 3xl:text-[89px];
  @apply -mt-[16px] 3xl:-mt-[22px];
}
.footer-features__item {
  @apply h-[103px] 2xl:h-[125px] 3xl:h-[140px];
  @apply flex items-center justify-center;
  @apply text-slate-40 hover:text-blue-900;
  @apply transition-all;
  @apply cursor-default;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.footer-features__item_first {
  @apply w-[355px] 2xl:w-[430px] 3xl:w-[480px];
  background-image: url("../img/footer-features/bg_1.svg");
  &:hover {
    background-image: url(data:image/svg+xml;base64,);
  }
}
.footer-features__item_second {
  @apply w-[433px] 2xl:w-[515px] 3xl:w-[590px];
  background-image: url("../img/footer-features/bg_2.svg");
  &:hover {
    background-image: url(data:image/svg+xml;base64,);
  }
}
.footer-features__item_third {
  @apply w-[265px] 2xl:w-[321px] 3xl:w-[359px];
  background-image: url("../img/footer-features/bg_3.svg");
  &:hover {
    background-image: url(data:image/svg+xml;base64,);
  }
}
