.contact-switcher {
}
.contact-switcher__item {
  @apply flex flex-nowrap items-center transition-[gap] gap-0 data-[active=true]:gap-3 xl:data-[active=true]:gap-4;
  @apply rounded-[18px] md:rounded-3xl lg:rounded-20;
  @apply h-auto xl:h-[74px] 2xl:h-24;
  @apply max-xl:p-3;
  &[data-active="true"] {
    @apply bg-white;
    @apply max-xl:flex-1;
  }
  &[data-active="false"] {
    @apply max-xl:inset-ring max-xl:inset-ring-white/20 max-xl:bg-white/20 max-xl:backdrop-blur-[13px];
  }
}
.contact-switch {
  @apply relative cursor-pointer group-[[data-active=true]]:cursor-default;
  @apply size-[56px] md:size-[64px] xl:size-[74px] 2xl:size-24;
}
.contact-switcher__info {
  @apply overflow-hidden transition-all w-0 opacity-0 group-[[data-active=true]]:w-[130px] lg:group-[[data-active=true]]:w-[170px] group-[[data-active=true]]:opacity-100;
  @apply flex flex-col gap-1 2xl:gap-2;
}
.contact-switcher__line1 {
  @apply text-blue-900 font-bold text-nowrap;
  @apply leading-6;
  @apply text-sm xl:text-base 2xl:text-lg;
}
.contact-switcher__line2 {
  @apply text-slate-430 hover:text-slate-430 font-medium text-nowrap;
  @apply text-xs xl:text-sm 2xl:text-base;
  @apply leading-4;
  @apply tracking-[0.12px] xl:tracking-[0.14px] 2xl:tracking-[0.16px];
}
.contact-switch__img {
  @apply absolute left-0 bottom-0;
  @apply w-[56px] h-[64px];
  @apply md:w-[64px] md:h-[68px];
  @apply xl:w-[74px] xl:h-[82px];
  @apply 2xl:w-[96px] 2xl:h-[110px];
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom center;
}
.contact-switch__img_dima {
  background-image: url("../img/contact-switcher/dima_440.png");
}
.contact-switch__img_tanya {
  background-image: url("../img/contact-switcher/tanya_440.png");
}
.contact-switch__img_olya {
  background-image: url("../img/contact-switcher/olya_440.png");
}
@media (min-width: 768px) {
  .contact-switch__img_dima {
    background-image: url("../img/contact-switcher/dima_768.png");
  }
  .contact-switch__img_tanya {
    background-image: url("../img/contact-switcher/tanya_768.png");
  }
  .contact-switch__img_olya {
    background-image: url("../img/contact-switcher/olya_768.png");
  }
}
@media (min-width: 1280px) {
  .contact-switch__img_dima {
    background-image: url("../img/contact-switcher/dima_1280.png");
  }
  .contact-switch__img_tanya {
    background-image: url("../img/contact-switcher/tanya_1280.png");
  }
  .contact-switch__img_olya {
    background-image: url("../img/contact-switcher/olya_1280.png");
  }
}
@media (min-width: 1440px) {
  .contact-switch__img_dima {
    background-image: url("../img/contact-switcher/dima_1440.png");
  }
  .contact-switch__img_tanya {
    background-image: url("../img/contact-switcher/tanya_1440.png");
  }
  .contact-switch__img_olya {
    background-image: url("../img/contact-switcher/olya_1440.png");
  }
}
.contact-switch__ring_dima {
  @apply absolute inset-0 transition-all;
  @apply ring-1 ring-white/50;
  @apply xl:ring-2 xl:ring-[#D6DEFA];
  @apply group-hover:ring-blue-600 group-[[data-active=true]]:ring-blue-600;
  @apply rounded-xl md:rounded-2xl;
}
.contact-switch__ring_tanya {
  @apply absolute left-1/2 -translate-x-1/2 rotate-45 transition-all;
  @apply rounded-10 size-[48px] bottom-[4px];
  @apply md:rounded-2xl md:size-[52px] md:bottom-[6px];
  @apply xl:rounded-2xl xl:size-[66px] xl:bottom-[7px];
  @apply 2xl:rounded-2xl 2xl:size-[80px] 2xl:bottom-[8px];
  @apply ring-1 ring-white/50;
  @apply xl:ring-2 xl:ring-[#EDD2F6];
  @apply group-hover:ring-purple-400 group-[[data-active=true]]:ring-purple-400;
}
.contact-switch__ring_olya {
  @apply absolute transition-all rounded-full;
  @apply inset-0;
  @apply ring-1 ring-white/50;
  @apply xl:ring-2 xl:ring-[#DCEFA8];
  @apply group-hover:ring-lime-500 group-[[data-active=true]]:ring-lime-500;
}
.contact-switch__label {
  @apply transition-colors absolute left-1/2 -translate-x-1/2;
  @apply -bottom-1 xl:-bottom-1.5 2xl:-bottom-2.5;
  @apply px-1 py-1.5 2xl:px-2 2xl:py-2.5;
  @apply rounded-md 2xl:rounded-xl;
  @apply shadow-contact-switcher-label;
  @apply font-bold text-nowrap;
  @apply text-[10px] leading-[7px] tracking-[0.1px];
  @apply xl:text-[8px] xl:leading-[6px] xl:tracking-[0.08px];
  @apply 2xl:text-xs 2xl:leading-[9px] 2xl:tracking-[0.12px];
  @apply bg-white text-blue-900;
}
.contact-switch__label_dima {
  @apply group-hover:text-blue-600 group-[[data-active=true]]:text-white group-[[data-active=true]]:bg-blue-600;
}
.contact-switch__label_tanya {
  @apply group-hover:text-purple-400 group-[[data-active=true]]:text-white group-[[data-active=true]]:bg-purple-400;
}
.contact-switch__label_olya {
  @apply group-hover:text-lime-500 group-[[data-active=true]]:text-blue-900 group-[[data-active=true]]:bg-lime-500;
}
.contact-switch__connector {
  @apply max-xl:hidden;
  @apply absolute left-1/2 -translate-x-1/2;
  @apply -bottom-5 2xl:-bottom-9;
  @apply w-[38px] h-2;
  @apply opacity-0 transition-opacity;
  @apply delay-0 duration-200;
  background-image: url("../img/contact-switcher/connector.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
.contact-switcher__item[data-active=true] .contact-switch__connector {
  @apply opacity-100;
  @apply delay-200 duration-200;
}
@media (max-width: 767px) {
  .contact-switcher-container {
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    -ms-overflow-style: none;
  }
  .contact-switcher-container::-webkit-scrollbar {
    display: none;
  }
}
